html {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
}

body {
  width: auto;
  height: auto;
  margin: 10px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: visible;
}

@media (orientation: landscape) {
  body {
    right: 50%;
  }
}

@media (orientation: portrait) {
  body {
    bottom: 50%;
  }
}

#theremin {
  width: auto;
  height: auto;
  box-sizing: border-box;
  background-color: #000;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: visible;
}

.fullscreen-button {
  width: 50px;
  height: 50px;
  color: gray;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.fullscreen-button.grow .shrink, .fullscreen-button.shrink .grow {
  display: none;
}

.GmrByW_pane {
  width: 100%;
  height: 100%;
  touch-action: none;
  box-sizing: border-box;
  position: relative;
  overflow: visible;
}

.GmrByW_pane * {
  touch-action: none;
}

.GmrByW_pointer {
  position: absolute;
}

.GmrByW_pointer > .GmrByW_ring {
  width: 60px;
  height: 60px;
  border: 4px solid #d3d3d3;
  border-radius: 50%;
